import React, { Component } from "react";
import Carousel from "../Components/Carousel"

export default class Slider extends Component {
  render() {
    return (
      <div style={Styles.sliderContainer}>
        <div style={Styles.sliderTextContainer}>
          <p style={Styles.sliderTextHead}>
            THPT Chuyn Lng Th Vinh community
          </p>
          <div style={Styles.sliderTextDescriptionContainer}>
            <p style={Styles.sliderTextDescription}>
              Vi s mnh l cng dng trin khai Biove, chng ti s ng hnh
              vi cc bn trn mi chng ng. Chin dch VN-Green t mc tiu
              trng 50.000 cy trong nm nm 2021.{" "}
            </p>
            <a style={Styles.sliderTextLink} href="#">
              Tham gia ng gp{" "}
            </a>
          </div>
        </div>
        <Carousel></Carousel>
      </div>
    );
  }
}

const Styles = {
  sliderContainer: {
    backgroundColor: "white",
    paddingTop: "7%",
  },
  sliderTextContainer: {
    marginLeft: "9%",
    display: "flex",
    marginBottom:"40px"
  },
  sliderTextHead: {
    fontSize: "40px",
    fontWeight: "500",
    display: "block",
    width: "35%",
  },
  sliderTextDescriptionContainer: {
    width: "40%",
    marginLeft: "50px",
    marginTop: "30px",
  },
  sliderTextDescription: {
    fontSize: "18px",
    fontWeight: "500",
  },
  sliderTextLink: {
    color: "#4A90E2",
    fontSize: "18px",
    fontWeight: "500",
    marginTop:"-20px",
    display:"block",
    textDecoration:"none"
  },
};
